.container {
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
}
.dropzone {
  flex: 1 1;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  -webkit-transition: border 0.24s ease-in-out;
  transition: border 0.24s ease-in-out;
}
.dropzone img {
  width: 36px;
  margin-right: 20px;
}
.dropzone .file-title {
  color: #000;
}
.dropzone .file-container {
  border: 1px solid #EEE;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 15px;
}
